<template>
  <el-container>
    <div class="subsystem-administrator-container" style="width: 100%">
      <div class="table-header-btn">
        <div class="table-header-btn-l">
          <!-- <el-button type="primary" size="small" @click="addSubsystem()">禁用</el-button>
            <el-button type="primary" size="small" @click="addSubsystem()">启用</el-button> -->
          <el-input v-model="pageObj.sysName" size="small" placeholder="请输入系统名称"></el-input>
          <el-button type="primary" size="small" @click="search()">检索</el-button>
        </div>
        <div class="table-header-btn-r">
          <el-button type="primary" size="small" @click="addSubsystem()">添加</el-button>
          <el-button type="primary" size="small" @click="moveDirection('up')">上移</el-button>
          <el-button type="primary" size="small" @click="moveDirection('down')">下移</el-button>
          <el-button type="primary" size="small" @click="sortSys()">保存排序</el-button>
        </div>
      </div>
      <el-table ref="subsystemIndexTable" :data="newDataList" :height="siteContentViewHeight - 100+'px'" @selection-change="selectionChangeHandle">
        <el-table-column type="selection" width="50"></el-table-column>
        <el-table-column label="序号" align="center" width="50">
          <template slot-scope="scope"> {{pageObj.pageIndex*pageObj.pageSize+scope.$index+1}} </template>
        </el-table-column>
        <el-table-column label="子系统代码" prop="sysCode"></el-table-column>
        <el-table-column label="子系统名称" prop="sysName"></el-table-column>
        <el-table-column label="子系统类型" prop="sysName"></el-table-column>
        <el-table-column label="子系统状态">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.sysStsatus == 0" size="small" type="danger">停用</el-tag>
            <el-tag v-else size="small">启用</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" plain @click="editSubsystem(scope.$index,scope.row)">修改</el-button>
            <el-button type="danger" size="mini" plain @click="deleSubsystem(scope.$index,scope.row)">删除</el-button>
          </template>
        </el-table-column>

      </el-table>
      <!-- 分页 -->
      <div>
        <pagination :pageIndex="pageObj.pageIndex" :total="pageObj.total" :pageSizes="[10, 20, 30, 40]" :pageSize="pageObj.pageSize" @PageTurning="PageTurning"></pagination>
      </div>
    </div>

    <!-- 添加、修改弹框 -->
    <el-dialog :title="title" width="50%" :visible.sync="addSubsystemVisiable" v-if='addSubsystemVisiable' :close-on-click-modal="false" :modal-append-to-body='false' append-to-body>
      <addsubsystemdialog @addback='addback' :checkedData="checkedData" :isEdit="isEdit"></addsubsystemdialog>
    </el-dialog>

  </el-container>
</template>

<script>
import { swapItems } from '@/utils';
import addsubsystemdialog from './add-subsystem-dialog.vue'
import pagination from '@/components/baseComponents/Pagination.vue'

export default {
  name: 'subsystem-administrator-index',//应用子系统管理
  props: ['siteContentViewHeight'],
  components: {
    addsubsystemdialog,
    pagination
  },
  data () {
    return {
      pageObj: {
        sysName: '',
        pageIndex: this.API.constObj.pageIndex, //当前页码
        pageSize: this.API.constObj.pageSize, //页大小
        total: 0, //数据总数
      },
      dataList: [],
      addSubsystemVisiable: false,//添加、修改弹框
      isEdit: '',
      checkedData: {},//编辑、删除选中数据
      tableDataSelections: [],
    }
  },
  computed: {
    newDataList () {
      let arr = this.dataList
      if (this.pageObj.sysName !== '') {
        arr = this.dataList.filter(item => {
          return item.sysName.indexOf(this.pageObj.sysName) > -1
        })
      }
      return arr
    }
  },
  created () {
    this.getDataList()
  },
  methods: {
    getDataList () {
      this.$http({
        url: this.$http.adornUrl(this.API.getApplySystemInfoSystem),
        method: 'post',
        data: this.$http.adornData({
          sysName: this.pageObj.sysName,
          size: this.pageObj.pageSize,
          current: this.pageObj.pageIndex + 1,
        })
      }).then((res) => {
        var resData = res.data;
        if (resData.code === 0) {
          this.dataList = resData.data.records;
          this.pageObj.total = resData.data.total;
        } else {
          this.$message.error(resData.msg);
        }
      })
    },
    //添加
    addSubsystem () {
      this.title = "添加";
      this.isEdit = true;
      this.addSubsystemVisiable = true;
    },
    //编辑
    editSubsystem (index, row) {
      this.checkedData = row;
      this.title = "编辑";
      this.isEdit = false;
      this.addSubsystemVisiable = true;
    },
    //删除
    deleSubsystem (index, row) {
      this.$confirm('确定删除选中记录?', '操作提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http({
          url: this.$http.adornUrl(this.API.deleteSystem),
          method: 'post',
          data: this.$http.adornData({
            sysId: row.sysId
          })
        }).then(({ data }) => {
          if (data.code === 0) {
            this.getDataList();
            this.$message.success('删除成功');
          } else {
            this.$message.error(data.msg);
          }
        })
      }).catch(() => {

      });
    },

    selectionChangeHandle (val) {//多选
      this.tableDataSelections = val;
    },
    moveDirection (direction) { //移动
      if (this.tableDataSelections.length === 0) {
        this.$confirm('请选择一条数据！', '提示', {
          confirmButtonText: '确定',
          showCancelButton: false,
          type: 'warning',
          center: true
        });
        return;
      };
      var that = this, index;
      that.tableDataSelections.sort((prev, next) => {
        return that.dataList.indexOf(prev) - that.dataList.indexOf(next)
      })
      if (direction == 'down') that.tableDataSelections.reverse()
      that.tableDataSelections.forEach(item => {
        index = that.dataList.indexOf(item); //得到右侧选中值的标识坐标
        if (direction == 'up') that.moveUp(index);
        else if (direction == 'down') that.moveDown(index)
      })
    },
    moveUp (index) {//上移
      if (index == 0) return;
      this.dataList = swapItems(this.dataList, index, index - 1, 'up');
    },
    moveDown (index) { //下移
      if (index == this.dataList.length - 1) return;
      this.dataList = swapItems(this.dataList, index, index + 1, 'down');
    },
    sortSys () {//保存排序
      var sortArr = [];
      if (this.dataList.length < 0) return;
      this.dataList.forEach((item, index) => {
        sortArr.push({
          id: item.sysId,
          orderno: index + 1
        })
      })
      this.$http({
        url: this.$http.adornUrl(this.API.systemSortSystem),
        method: 'post',
        data: this.$http.adornData({
          data: JSON.stringify(sortArr)
        })
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.getDataList()
          this.$message.success('操作成功');
        } else {
          this.$message.error(data.msg);
        }
      });
    },

    search () {
      this.pageObj.pageIndex = this.API.constObj.pageIndex;
      this.getDataList()
    },

    // 添加或修改组件回调方法--子组件回调数据的方法
    // @param {Boolean} isRefresh是否需要刷新数据 true=刷新；false=不刷新
    addback (isRefresh) {
      this.addSubsystemVisiable = false;
      if (isRefresh) {
        this.getDataList();
      }
    },
    //分页组件回调方法
    PageTurning (page_obj) {
      this.pageObj.pageIndex = page_obj.page_index;
      this.pageObj.pageSize = page_obj.page_size;
      this.getDataList();
    },
  }
}
</script>

<style scoped lang="scss">
.table-header-btn-l {
  .el-input {
    margin-right: 10px;
  }
}
</style>



